import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Button, Checkbox, FormControlLabel } from '@material-ui/core';
import { calcDetails } from 'utils/calcStats';
import { resetStats } from 'actions';
import { dungeonInfo } from 'data';
import StatsTable from './StatsTable';
import Dungeon from './Dungeon';

// const SECTIONS = ['build', 'team'];

// eslint-disable-next-line no-shadow
function Col1({ stats, halidom, resetStats }) {
  const [collapse, setCollapse] = useState(false);

  const handleCollapse = e => {
    setCollapse(e.target.checked);
  };

  const [dungeon, setDungeon] = useState('hzd');
  const [multiplier, setMultiplier] = useState(dungeonInfo[dungeon].multiplier);

  const details = React.useMemo(() => calcDetails(stats, halidom), [
    stats,
    halidom,
  ]);

  return (
    <div id="col1">
      <div className="col-2">
        <FormControlLabel
          control={
            <Checkbox
              checked={collapse}
              disabled={!details}
              onChange={handleCollapse}
            />
          }
          label="CHECK"
        />
        <Button variant="outlined" onClick={resetStats}>
          RESET
        </Button>
      </div>
      <StatsTable details={details} collapse={collapse} />
      {stats.adventurer && collapse && (
        <Dungeon
          dungeon={dungeon}
          multiplier={multiplier}
          setDungeon={setDungeon}
          setMultiplier={setMultiplier}
          totalHp={details.total.hp}
          stats={stats}
        />
      )}
    </div>
  );
}

const mapStateToProps = ({ stats, halidom }) => {
  return { stats, halidom };
};

const actionCreators = {
  resetStats,
};

export default connect(
  mapStateToProps,
  actionCreators
)(Col1);
